import GLOBAL from "../../../service.js"
export default {
	data() {
		return {
			userData:'',
			loginType:false,
		}
	},
	onLoad(){
		this.userData=uni.getStorageSync("userData");
		console.log(this.userData)
	},
	methods: {
		toComparison(){
			if(this.loginType==true){
			uni.navigateTo({
				url:'../comparison'
			})
			}
			else{
				uni.navigateTo({
					url:'../test'
				})
			}
		},
		getString(){
			// if(uni.getStorageSync('sid')==''){
			// 	this.loginType=false
			// 	return "请登录"
			// }
			// this.loginType=true
			// return '退出登录'
			uni.request({
				url: GLOBAL.BASE+'/isAlive',
				header:{
					"Cookie":"JSESSIONID="+uni.getStorageSync("sid")
				},
				success: res => {
					if(res.data==true)
					{
						this.loginType=true
					}
					else{
						this.loginType=false
					}
				}
			})
			if(this.loginType)
			{
				return "退出登录";
			}
			else{
				return "请登录"
			}
		},
		toReset(){
			if(this.loginType==false)
			{
				uni.navigateTo({
					url:'../test'
				})
			}
			else{
				uni.request({
					url:GLOBAL.BASE+'/logout',
					header:{
						"Cookie":"JSESSIONID="+uni.getStorageSync("sid")
					}
				})
				uni.clearStorage();
				uni.reLaunch({
					url:'../Team'
				})
			}
		}
		
	}
}
